import React from "react";
import axios from "axios";
import { API_URL } from "../utils/API_URl";

const FileUploadManagement = async (props) => {
  const { inputData, file, formField, formInputValues, token } = props;
  let attachments = {};
  const attachMent = (att = null) => {
    props.cf(inputData, att);
  };
  const xx = await formField.map(async (item, i) => {
    let itemData = item.split("-");
    let itemField = itemData[0];
    if (itemData[1] === "image" || itemData[1] === "file") {
      if (file[itemField] === null || file[itemField] === "") {
        return null;
      } else if (file[itemField] === "No File") {
        let preFieldData = formInputValues[itemField];
        if (preFieldData !== "" && preFieldData !== null) {
          let preFileExt = preFieldData.split(".").splice(-1)[0];

          if (["jpg", "jpeg", "png", "gif"].includes(preFileExt) === true) {
            await axios.delete(
              `${API_URL}/api/deleteFiles/uploads/${preFieldData}`
            );
          } else {
            await axios.delete(
              `${API_URL}/api/deleteFiles/uploadedFiles/${preFieldData}`
            );
          }
        }
        attachments = { ...attachments, [itemField]: null };
        console.log(attachments);
        //attachMent(attachments);
      } else {
        const formData = new FormData();
        await formData.append("image", file[itemField]);
        if (typeof file[itemField] === "object") {
          let formFileExt = file[itemField].name.split(".").splice(-1)[0];
          if (["jpg", "jpeg", "png", "gif"].includes(formFileExt) === true) {
            const res = await axios.post(`${API_URL}/api/upload/`, formData, {
              headers: {
                "Content-Type": "multipart/form-data",
                token,
              },
            });
            attachments = {
              ...attachments,
              [itemField]: res.data.filename,
            };
            return attachments;
          } else {
            const res = await axios.post(
              `${API_URL}/api/uploadDoc/`,
              formData,
              {
                headers: {
                  "Content-Type": "multipart/form-data",
                  token,
                },
              }
            );
            attachments = {
              ...attachments,
              [itemField]: res.data.filename,
            };
            return attachments;
          }
        }

        if (
          inputData[itemField] !== null &&
          inputData[itemField] !== "" &&
          inputData[itemField] !== undefined &&
          inputData[itemField] !== file[itemField]
        ) {
          let inputFileExt = inputData[itemField].split(".").splice(-1)[0];

          if (["jpg", "jpeg", "png", "gif"].includes(inputFileExt) === true) {
            await axios.delete(
              `${API_URL}/api/deleteFiles/uploads/${inputData[itemField]}`
            );
          } else {
            await axios.delete(
              `${API_URL}/api/deleteFiles/uploadedFiles/${inputData[itemField]}`
            );
          }
        }
      }
      return attachments;
    }
  });
  Promise.all(xx)
    .then((results) => {
      attachMent(attachments);
    })
    .catch((error) => {
      console.error("Error:", error);
    });
};

export default FileUploadManagement;
