import React, { useState, useEffect } from "react";
import Base from "../Base";
import { useSelector, useDispatch } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";
import {
  Col,
  Row,
  Accordion,
  AccordionBody,
  AccordionHeader,
  AccordionItem,
} from "reactstrap";

import EditHomeContent1 from "../components/ProfileView/EditHomeContent1";
import EditHomeContent2 from "../components/ProfileView/EditHomeContent2";
import EditHomeTopBanner from "../components/ProfileView/EditHomeTopBanner";
import EditMiddleBanner from "../components/ProfileView/EditMiddleBanner";
import EditUserProfile from "../components/ProfileView/EditUserProfile";
import HomeContent1 from "../components/ProfileView/HomeContent1";
import HomeContent2 from "../components/ProfileView/HomeContent2";
import HomeTopBanner from "../components/ProfileView/HomeTopBanner";
import MiddleBanner from "../components/ProfileView/MiddleBanner";
import UserProfile from "../components/ProfileView/UserProfile";

import { getHomeData } from "../reducers/homeReducer";
import { ErrorAlert, SuccessAlert } from "../shared-components/AlertBox";
import { LoaderSM } from "../shared-components/loader";
import ProfileService from "../components/ProfileView/ProfileService";
import ProfileSkills from "../components/ProfileView/ProfileSkills";
import ProfileEmployer from "../components/ProfileView/ProfileEmployer";
import ProfileAcademia from "../components/ProfileView/ProfileAcademia";
import ProfileProject from "../components/ProfileView/ProfileProject";
import AnimatedElement from "../shared-components/AnimatedElement";

const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";
const Profile = () => {
  const { error, loading, success, userdetails } = useSelector(
    (state) => state.user
  );
  const { homeScreen } = useSelector((state) => state.home);

  const token = userdetails.token;

  const [editProfile, setEditProfile] = useState(false);
  const [editHomeTopBanner, setEditHomeTopBanner] = useState(false);
  const [editHomeContent1, setEditHomeContent1] = useState(false);
  const [editMiddleBanner, setEditMiddleBanner] = useState(false);
  const [editHomeContent2, setEditHomeContent2] = useState(false);
  // const [editServices, setEditServices] = useState(false);
  // const [editSkills, setEditSkills] = useState(false);
  const [userProfile, setUserProfile] = useState({});
  const [homeData, setHomeData] = useState(homeScreen);

  const dispatch = useDispatch();
  const [open, setOpen] = useState("");
  const toggle = (id) => {
    if (open === id) {
      setOpen();
    } else {
      setOpen(id);
    }
  };

  const getUserData = async () => {
    if (token) {
      const { data } = await axios.get(`${API_URL}/api/user/info/${token}`);
      if (!data.error) {
        await setUserProfile(data);
      } else {
        await setUserProfile({});
      }
    } else {
      await setUserProfile({});
    }
  };

  const getHomeScreenData = async () => {
    const data = await dispatch(getHomeData({ token: token }));
    if (!data.error) {
      await setHomeData(data.payload);
    } else {
      await setHomeData({});
    }
  };

  useEffect(() => {
    getUserData();
    getHomeScreenData();
  }, [userdetails]);

  const cancelEditProfile = (value) => {
    setEditProfile(value);
  };
  const cancelEditTopBanner = async (value) => {
    await getHomeScreenData();
    setEditHomeTopBanner(value);
  };
  const cancelEditContent1 = async (value) => {
    await getHomeScreenData();
    setEditHomeContent1(value);
  };
  const cancelEditMiddleBanner = async (value) => {
    await getHomeScreenData();
    setEditMiddleBanner(value);
  };
  const cancelEditContent2 = async (value) => {
    await getHomeScreenData();
    setEditHomeContent2(value);
  };
  const updateData = (hData) => {
    setHomeData({ ...hData });
  };

  return (
    <Base>
      <div className="container-fluid p-md-5 profile">
        <h4 className="p-color">Personal Info:</h4>

        <Row>
          <Col md={2}></Col>
          <Col md={10}>
            <AnimatedElement>
              <div className="position-relative t-back2 p-3">
                <UserProfile userProfile={userProfile} />
                <FontAwesomeIcon
                  icon={faEdit}
                  onClick={(e) => setEditProfile(true)}
                  className="mt-1 edit-icon"
                  style={{ padding: "7px 7px" }}
                />
              </div>
            </AnimatedElement>
          </Col>
        </Row>
        {editProfile ? (
          <EditUserProfile
            userProfile={userProfile}
            cancelEditProfile={cancelEditProfile}
          />
        ) : (
          ""
        )}

        {homeData && (
          <>
            <h4 className="p-color">Home Page:</h4>
            <Row>
              <Col md={2}></Col>
              <Col md={10}>
                <Accordion
                  className="p-color"
                  flush
                  open={open}
                  toggle={toggle}
                >
                  {homeData.banner && (
                    <AccordionItem>
                      <AccordionHeader targetId="1">
                        Top Banner:
                      </AccordionHeader>
                      <AccordionBody accordionId="1">
                        <div className="position-relative">
                          {!editHomeTopBanner ? (
                            <>
                              <HomeTopBanner
                                homeData={homeData}
                                updateData={updateData}
                              />
                              <FontAwesomeIcon
                                icon={faEdit}
                                onClick={(e) => setEditHomeTopBanner(true)}
                                className="mt-1 edit-icon"
                                style={{ padding: "7px 7px" }}
                              />
                            </>
                          ) : (
                            <EditHomeTopBanner
                              homeData={homeData}
                              cancelEditTopBanner={cancelEditTopBanner}
                            />
                          )}
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                  {homeData.pageContent1 && (
                    <AccordionItem>
                      <AccordionHeader targetId="2">
                        Home Content 1
                      </AccordionHeader>
                      <AccordionBody accordionId="2">
                        <div className="position-relative">
                          {!editHomeContent1 ? (
                            <>
                              <HomeContent1
                                homeData={homeData}
                                updateData={updateData}
                              />
                              <FontAwesomeIcon
                                icon={faEdit}
                                onClick={(e) => setEditHomeContent1(true)}
                                className="mt-1 edit-icon"
                                style={{ padding: "7px 7px" }}
                              />
                            </>
                          ) : (
                            <EditHomeContent1
                              homeData={homeData}
                              cancelEditContent1={cancelEditContent1}
                            />
                          )}
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                  {homeData && homeData.services && (
                    <AccordionItem>
                      <AccordionHeader targetId="3">Services</AccordionHeader>
                      <AccordionBody accordionId="3">
                        <div className="position-relative">
                          <ProfileService
                            homeData={homeData}
                            updateData={updateData}
                          />
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                  {homeData && homeData.middleBanner && (
                    <AccordionItem>
                      <AccordionHeader targetId="4">
                        Middle Banner
                      </AccordionHeader>
                      <AccordionBody accordionId="4">
                        <div
                          className="position-relative"
                          style={{ overflowX: "hidden" }}
                        >
                          {!editMiddleBanner ? (
                            <>
                              <MiddleBanner
                                homeData={homeData}
                                updateData={updateData}
                              />
                              <FontAwesomeIcon
                                icon={faEdit}
                                onClick={(e) => setEditMiddleBanner(true)}
                                className="mt-1 edit-icon"
                                style={{ padding: "7px 7px" }}
                              />
                            </>
                          ) : (
                            <EditMiddleBanner
                              homeData={homeData}
                              cancelEditMiddleBanner={cancelEditMiddleBanner}
                            />
                          )}
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                  {homeData && homeData.pageContent2 && (
                    <AccordionItem>
                      <AccordionHeader targetId="5">
                        Home Content 2
                      </AccordionHeader>
                      <AccordionBody accordionId="5">
                        <div
                          className="position-relative"
                          style={{ overflowX: "hidden" }}
                        >
                          {!editHomeContent2 ? (
                            <>
                              <HomeContent2
                                homeData={homeData}
                                updateData={updateData}
                              />
                              <FontAwesomeIcon
                                icon={faEdit}
                                onClick={(e) => setEditHomeContent2(true)}
                                className="mt-1 edit-icon"
                                style={{ padding: "7px 7px" }}
                              />
                            </>
                          ) : (
                            <EditHomeContent2
                              homeData={homeData}
                              cancelEditContent2={cancelEditContent2}
                            />
                          )}
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                  {homeData && homeData.skills && (
                    <AccordionItem>
                      <AccordionHeader targetId="6">Skills</AccordionHeader>
                      <AccordionBody accordionId="6">
                        <div className="position-relative">
                          <ProfileSkills
                            homeData={homeData}
                            updateData={updateData}
                          />
                        </div>
                      </AccordionBody>
                    </AccordionItem>
                  )}
                </Accordion>
              </Col>
            </Row>
          </>
        )}

        <h4 className="p-color">Employers:</h4>
        <Row className="my-3">
          <Col className="col-12 col-md-2"></Col>
          <Col className="col-12 col-md-10">
            <div className="t-back2">
              <ProfileEmployer />
            </div>
          </Col>
        </Row>

        <h4 className="p-color">Academia:</h4>
        <Row className="my-3">
          <Col className="col-12 col-md-2"></Col>
          <Col className="col-12 col-md-10">
            <div className="t-back2">
              <ProfileAcademia />
            </div>
          </Col>
        </Row>

        <h4 className="p-color">Projects:</h4>
        <Row className="my-3">
          <Col className="col-12 col-md-2"></Col>
          <Col className="col-12 col-md-10">
            <div className="t-back2">
              <ProfileProject />
            </div>
          </Col>
        </Row>
      </div>
      {loading && <LoaderSM />}
      {error && <ErrorAlert message={error} />}
      {success && <SuccessAlert message={success} />}
    </Base>
  );
};

export default Profile;
