import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";

import { Button, Col, Row } from "reactstrap";
import AddEmployerComponentModal from "../../shared-components/AddEmployerComponentModal";
import {
  deleteEmployerReducer,
  getEmployerData,
  updateEmployerReducer,
} from "../../reducers/employerReducer";
import ProfileEmployerSection from "../../shared-components/ProfileEmployerSection";

const ProfileEmployer = () => {
  const { userdetails } = useSelector((state) => state.user);
  const token = userdetails.token;

  const [employerData, setEmployerData] = useState(null);
  const [addEmployerModal, setAddEmployerModal] = useState(false);
  const [updateEmployer, setUpdateEmployer] = useState(true);
  const [formFieldValues, setFormFieldValues] = useState({});
  const dispatch = useDispatch();
  const formFieldTypes = [
    "employerName-text-require",
    "startDate-date-optional",
    "endDate-date-optional",
  ];
  const getEmployer = async () => {
    const data = await dispatch(getEmployerData({ token: token }));
    if (!data.error && data.payload !== {}) {
      await setEmployerData(data.payload);
    } else {
      await setEmployerData(null);
    }
  };
  const closeModal = async (data, qType) => {
    setAddEmployerModal(false);
    setFormFieldValues({});
    if (data && qType === "updated") {
      let tempEmployer = [...employerData];
      await employerData.forEach((item, i) => {
        if (item._id === data.payload._id) {
          tempEmployer[i] = data.payload;
        }
      });
      setEmployerData([...tempEmployer]);
    } else if (data && qType === "created") {
      employerData
        ? setEmployerData([data.payload, ...employerData])
        : setEmployerData([data.payload]);
    } else {
      setUpdateEmployer(true);
    }
  };
  const openModal = () => {
    setAddEmployerModal(true);
    setUpdateEmployer(false);
  };
  useEffect(() => {
    getEmployer();
  }, [userdetails, updateEmployer]);

  const updateStatus = async (id, active) => {
    const data = await dispatch(
      updateEmployerReducer({
        _id: id,
        active: active,
        token: token,
      })
    );
    if (data) {
      let tempEmployer = [...employerData];
      await employerData.forEach((item, i) => {
        if (item._id === data.payload._id) {
          tempEmployer[i] = data.payload;
        }
      });
      setEmployerData(tempEmployer);
    }
  };

  const editHandler = async (item) => {
    await setFormFieldValues({
      ...item,
    });
    setAddEmployerModal(true);
  };

  const deleteHandler = async (item) => {
    const data = await dispatch(
      deleteEmployerReducer({ ...item, token: token })
    );
    if (data) {
      let updatedData = employerData.filter(
        (item, i) => item._id !== data.payload._id
      );
      setEmployerData(updatedData);
    }
  };

  return (
    <div className="py-3" style={{ overflowX: "hidden" }}>
      <Row>
        <Col className="text-end px-4">
          <Button
            className="sp-btn-secondary rounded-0 px-5"
            onClick={openModal}
          >
            Add New Employer
          </Button>
        </Col>
      </Row>
      {addEmployerModal && (
        <AddEmployerComponentModal
          setAddEmployerModal={closeModal}
          formField={formFieldTypes}
          formInputValues={formFieldValues}
        />
      )}
      {employerData &&
        employerData.map((item, i) => (
          <ProfileEmployerSection
            item={item}
            key={item._id}
            updateStatus={updateStatus}
            editHandler={editHandler}
            deleteHandler={deleteHandler}
          />
        ))}
    </div>
  );
};

export default ProfileEmployer;
