import React, { useEffect, useState } from "react";
import { Col, FormGroup, Input, Row } from "reactstrap";
import parse from "html-react-parser";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../../reducers/homeReducer";
import { useInView } from "react-intersection-observer";
import FileComponent from "../../shared-components/FileComponent";

const MiddleBanner = (props) => {
  const homeData = props.homeData;
  const { userdetails } = useSelector((state) => state.user);
  const [middleBanner, setMiddleBanner] = useState(props.homeData.middleBanner);
  const dispatch = useDispatch();

  const { ref, inView } = useInView({
    triggerOnce: true, // Only trigger the animation once
    threshold: 0.1, // Percentage of section visibility to trigger the animation
  });

  const updateStatus = async () => {
    await dispatch(
      updateHomeReducer({
        ...homeData,
        middleBanner: { ...middleBanner, active: !middleBanner.active },
        token: userdetails.token,
      })
    );

    props.updateData({
      ...homeData,
      middleBanner: { ...middleBanner, active: !middleBanner.active },
    });
    setMiddleBanner({ ...middleBanner, active: !middleBanner.active });
  };

  useEffect(() => {
    setMiddleBanner(props.homeData.middleBanner);
  }, [props]);

  return (
    <div ref={ref} className={inView ? "animated-section" : "hidden-section"}>
      <Row>
        <Col sx={6}>Status</Col>
        <Col sx={6}>
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={middleBanner.active}
              onChange={updateStatus}
            />
            {middleBanner.active ? "Active" : "Inactive"}
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col sx={6}>Banner Content</Col>
        <Col sx={6}>{parse(`${middleBanner.bannerText}`)}</Col>
      </Row>
      <Row>
        <Col sx={6}>Banner Image</Col>
        <Col sx={6}>
          {middleBanner.bannerImage ? (
            <FileComponent
              fileSrc={middleBanner.bannerImage}
              alt={middleBanner.bannerText}
              fileStyle={{ maxHeight: "80px" }}
            />
          ) : (
            ""
          )}
        </Col>
      </Row>
    </div>
  );
};

export default MiddleBanner;
