import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import AddAcademyComponentModal from "../../shared-components/AddAcademyComponentModal";
import {
  deleteAcademyReducer,
  getAcademyData,
  updateAcademyReducer,
} from "../../reducers/academyReducer";
import ProfileAcademySection from "../../shared-components/ProfileAcademySection";

const ProfileAcademia = () => {
  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [addAcademyModal, setAddAcademyModal] = useState(false);
  const [updateAcademy, setUpdateAcademy] = useState(true);
  const [formFieldValues, setFormFieldValues] = useState({});
  const [academyData, setAcademyData] = useState(null);

  const formFieldTypes = [
    "academyName-text-require",
    "instituteName-text-optional",
    "passingYear-text-optional",
  ];

  const getAcademy = async () => {
    const data = await dispatch(getAcademyData({ token: userdetails.token }));
    if (!data.error && data.payload !== {}) {
      await setAcademyData(data.payload);
    } else {
      await setAcademyData(null);
    }
  };

  const openModal = () => {
    setAddAcademyModal(true);
    setUpdateAcademy(false);
  };

  const closeModal = async (data, qType) => {
    setAddAcademyModal(false);
    setFormFieldValues({});
    if (data && qType === "updated") {
      let tempAcademy = [...academyData];
      await academyData.forEach((item, i) => {
        if (item._id === data.payload._id) {
          tempAcademy[i] = data.payload;
        }
      });
      setAcademyData([...tempAcademy]);
    } else if (data && qType === "created") {
      academyData
        ? setAcademyData([data.payload, ...academyData])
        : setAcademyData([data.payload]);
    } else {
      setUpdateAcademy(true);
    }
  };

  const updateStatus = async (id, active) => {
    const data = await dispatch(
      updateAcademyReducer({
        _id: id,
        active: active,
        token: userdetails.token,
      })
    );
    if (data) {
      let tempAcademy = [...academyData];
      await academyData.forEach((item, i) => {
        if (item._id === data.payload._id) {
          tempAcademy[i] = data.payload;
        }
      });
      setAcademyData(tempAcademy);
    }
  };

  const editHandler = async (item) => {
    await setFormFieldValues({
      ...item,
    });
    setAddAcademyModal(true);
  };

  const deleteHandler = async (item) => {
    const data = await dispatch(
      deleteAcademyReducer({ ...item, token: userdetails.token })
    );
    if (data) {
      let updatedData = academyData.filter(
        (item, i) => item._id !== data.payload._id
      );
      setAcademyData(updatedData);
    }
  };

  useEffect(() => {
    getAcademy();
  }, [userdetails, updateAcademy]);

  return (
    <div className="py-3" style={{ overflowX: "hidden" }}>
      <Row>
        <Col className="text-end px-4">
          <Button
            className="sp-btn-secondary rounded-0 px-5"
            onClick={openModal}
          >
            Add Academy
          </Button>
        </Col>
      </Row>
      {addAcademyModal && (
        <AddAcademyComponentModal
          setAddAcademyModal={closeModal}
          formField={formFieldTypes}
          formInputValues={formFieldValues}
        />
      )}

      {academyData &&
        academyData.map((item, i) => (
          <ProfileAcademySection
            item={item}
            key={item._id}
            updateStatus={updateStatus}
            editHandler={editHandler}
            deleteHandler={deleteHandler}
          />
        ))}
    </div>
  );
};

export default ProfileAcademia;
