import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import AddProjectComponentModal from "../../shared-components/AddProjectComponentModal";
import {
  deleteProjectReducer,
  getProjectData,
  updateProjectReducer,
} from "../../reducers/projectReducer";

import ProfileProjectSection from "../../shared-components/ProfileProjectSection";

const ProfileProject = () => {
  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [addProjectModal, setAddProjectModal] = useState(false);
  const [updateProject, setUpdateProject] = useState(true);
  const [formFieldValues, setFormFieldValues] = useState({});
  const [projectData, setProjectData] = useState(null);

  const formFieldTypes = [
    "projectName-text-require",
    "responsibility-textarea-optional",
    "technologies-textarea-optional",
  ];

  const getProject = async () => {
    const data = await dispatch(getProjectData({ token: userdetails.token }));
    if (!data.error && data.payload !== {}) {
      await setProjectData(data.payload);
    } else {
      await setProjectData(null);
    }
  };

  const openModal = () => {
    setAddProjectModal(true);
    setUpdateProject(false);
  };

  const closeModal = async (data, qType) => {
    setAddProjectModal(false);
    setFormFieldValues({});
    if (data && qType === "updated") {
      let tempProject = [...projectData];
      await projectData.forEach((item, i) => {
        if (item._id === data.payload._id) {
          tempProject[i] = data.payload;
        }
      });
      setProjectData([...tempProject]);
    } else if (data && qType === "created") {
      projectData
        ? setProjectData([data.payload, ...projectData])
        : setProjectData([data.payload]);
    } else {
      setUpdateProject(true);
    }
  };

  const updateStatus = async (id, active) => {
    const data = await dispatch(
      updateProjectReducer({
        _id: id,
        active: active,
        token: userdetails.token,
      })
    );
    if (data) {
      let tempProject = [...projectData];
      await projectData.forEach((item, i) => {
        if (item._id === data.payload._id) {
          tempProject[i] = data.payload;
        }
      });
      setProjectData(tempProject);
    }
  };

  const editHandler = async (item) => {
    await setFormFieldValues({
      ...item,
    });
    setAddProjectModal(true);
  };

  const deleteHandler = async (item) => {
    const data = await dispatch(
      deleteProjectReducer({ ...item, token: userdetails.token })
    );
    if (data) {
      let updatedData = projectData.filter(
        (item, i) => item._id !== data.payload._id
      );
      setProjectData(updatedData);
    }
  };

  useEffect(() => {
    getProject();
  }, [userdetails, updateProject]);

  return (
    <div className="py-3" style={{ overflowX: "hidden" }}>
      <Row>
        <Col className="text-end px-4">
          <Button
            className="sp-btn-secondary rounded-0 px-5"
            onClick={openModal}
          >
            Add Project
          </Button>
        </Col>
      </Row>
      {addProjectModal && (
        <AddProjectComponentModal
          setAddProjectModal={closeModal}
          formField={formFieldTypes}
          formInputValues={formFieldValues}
        />
      )}

      {projectData &&
        projectData.map((item, i) => (
          <ProfileProjectSection
            item={item}
            key={item._id}
            updateStatus={updateStatus}
            editHandler={editHandler}
            deleteHandler={deleteHandler}
          />
        ))}
    </div>
  );
};

export default ProfileProject;
