import React, { useState } from "react";
import { Button, Col, FormGroup, Input, Row } from "reactstrap";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../../reducers/homeReducer";
import { ErrorAlert, SuccessAlert } from "../../shared-components/AlertBox";
import Services from "../../shared-components/Services";
import FormElementComponent from "../../shared-components/FormElementComponent";
import { createUserServicesData } from "../../reducers/serviceReducer";
import FileUploadManagement from "../../shared-components/FileUploadManagement";

const ProfileService = (props) => {
  const { homeData } = props;
  const { userdetails } = useSelector((state) => state.user);
  const [services, setServices] = useState(homeData.services);
  const [addServiceModal, setAddServiceModal] = useState(false);
  const [updateService, setUpdateService] = useState(true);
  const { error, success } = {};
  const formField = [
    "serviceName-text-require",
    "serviceHeading-text-optional",
    "serviceImage-image-optional",
    "serviceDesc-quill-optional",
  ];
  const [formInputValues, setFormInputValues] = useState({});
  const dispatch = useDispatch();

  const updateStatus = async () => {
    await dispatch(
      updateHomeReducer({
        ...homeData,
        services: { ...services, active: !services.active },
        token: userdetails.token,
      })
    );
    props.updateData({
      ...homeData,
      services: { ...services, active: !services.active },
    });
    setServices({ ...services, active: !services.active });
  };

  const closeModal = () => {
    setAddServiceModal(false);
    setUpdateService(true);
    setFormInputValues({});
  };
  const openModal = () => {
    setAddServiceModal(true);
    setUpdateService(false);
  };

  const updateInputData = async (x = null, y = null) => {
    if (x !== null) {
      console.log("create service data");
      const data = await dispatch(
        createUserServicesData({
          ...x,
          ...y,
          token: userdetails.token,
          id: formInputValues.id ? formInputValues.id : null,
        })
      );
      if (data.payload) {
        closeModal();
      }
    }
  };

  const createData = (inputData, file) => {
    FileUploadManagement({
      inputData,
      file,
      formField,
      formInputValues,
      token: userdetails.token,
      cf: updateInputData,
    });
  };

  const updateData = (inputData, file) => {
    FileUploadManagement({
      inputData,
      file,
      formField,
      formInputValues,
      token: userdetails.token,
      cf: updateInputData,
    });
  };

  const editServices = async (sName, sHeading, sImage, sDesc, id) => {
    await setFormInputValues({
      ...formInputValues,
      serviceName: sName,
      serviceHeading: sHeading,
      serviceImage: sImage,
      serviceDesc: sDesc,
      id,
    });
    setAddServiceModal(true);
    setUpdateService(false);
  };

  return (
    <>
      <Row>
        <Col className="col-6 col-md-3 order-2 order-md-1">Status :</Col>
        <Col className="col-6 col-md-3 order-2 order-md-1">
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={services.active}
              onChange={updateStatus}
            />
            {services.active ? "Active" : "Inactive"}
          </FormGroup>
        </Col>
        <Col className="text-end col-12 col-md-6 order-1 order-md-2 pb-3 pb-md-0">
          <Button
            className="sp-btn-secondary rounded-0 px-5"
            onClick={openModal}
          >
            Add New Service
          </Button>
        </Col>
      </Row>

      {addServiceModal && (
        <FormElementComponent
          formField={formField}
          formInputValues={formInputValues}
          modalHeading={
            formInputValues.id ? "Update Service" : "Add New Service"
          }
          buttons={[
            {
              type: "cancel",
              title: "Cancel",
            },
            {
              type: formInputValues.id ? "update" : "create",
              title: formInputValues.id ? "Update Service" : "Add New Service",
            },
          ]}
          closeModal={closeModal}
          createData={createData}
          updateData={updateData}
          formType="modal"
        />
      )}
      <Services
        displayType="list"
        updateService={updateService}
        editServices={editServices}
      />
      {error && <ErrorAlert message={error} />}
      {success && <SuccessAlert message={success} />}
    </>
  );
};

export default ProfileService;
