import React, { useRef, useState } from "react";
import "react-quill/dist/quill.snow.css";
import { useDispatch, useSelector } from "react-redux";
import FormElementComponent from "../../shared-components/FormElementComponent";
import FileUploadManagement from "../../shared-components/FileUploadManagement";
import { updateProfileReducer } from "../../reducers/authReducer";
const API_URL = process.env.REACT_APP_API_URL
  ? process.env.REACT_APP_API_URL
  : "";

const EditUserProfile = (props) => {
  const dispatch = useDispatch();
  const { userdetails } = useSelector((state) => state.user);
  const [updatedProfile, setUpdatedProfile] = useState({
    ...props.userProfile,
  });
  const formField = [
    "pic-image-optional",
    "name-text-optional",
    "mobile-text-optional",
    "mobile2-text-optional",
    "dob-text-optional",
    "language-text-optional",
    "strength-text-optional",
    "skypeId-text-optional",
    "permanentAddress-quill-optional",
    "careerObjective-quill-optional",
    "careerSummary-quill-optional",
    "personalQualities-quill-optional",
  ];
  const formInputValues = updatedProfile;

  const closeModal = () => {
    props.cancelEditProfile(false);
  };

  const updateInputData = async (x = null, y = null) => {
    if (x !== null) {
      const data = await dispatch(
        updateProfileReducer({
          ...x,
          ...y,
          token: userdetails.token,
        })
      );
      if (data.payload) {
        closeModal();
      }
    }
  };

  const createData = () => {};

  const updateData = async (inputData, file) => {
    FileUploadManagement({
      inputData,
      file,
      formField,
      formInputValues,
      token: userdetails.token,
      cf: updateInputData,
    });
  };

  return (
    <div>
      <FormElementComponent
        formField={formField}
        formInputValues={formInputValues}
        modalHeading={"Update Home Top Banner"}
        buttons={[
          {
            type: "cancel",
            title: "Cancel",
          },
          {
            type: "update",
            title: "Update",
          },
        ]}
        closeModal={closeModal}
        createData={createData}
        updateData={updateData}
        formType="modal"
      />
    </div>
  );
};

export default EditUserProfile;
