import parse from "html-react-parser";
import React from "react";
import { Col, Row } from "reactstrap";
import FileComponent from "../../shared-components/FileComponent";

const UserProfile = ({ userProfile }) => {
  return (
    <>
      <Row>
        <Col>
          {userProfile.pic ? (
            <FileComponent
              fileSrc={userProfile.pic}
              fileStyle={{}}
              fileClass="profile_pic"
              alt={userProfile.name}
            />
          ) : (
            <img
              src="/images/user-avatar.jpg"
              alt={userProfile.name}
              className="profile_pic"
            />
          )}
        </Col>
      </Row>
      <Row className="p-color">
        <Col className="col-4 col-md-2">Name :</Col>
        <Col className="col-8 col-md-4 t-color">{userProfile.name}</Col>
        <Col className="col-4 col-md-2">Email Id : </Col>
        <Col className="col-8 col-md-4 t-color">{userProfile.email}</Col>
        <Col className="col-4 col-md-2">Mobile : </Col>
        <Col className="col-8 col-md-4 t-color">
          {userProfile.mobile} , {userProfile.mobile2}
        </Col>
        <Col className="col-4 col-md-2">D.O.B : </Col>
        <Col className="col-8 col-md-4 t-color">{userProfile.dob}</Col>
        <Col className="col-4 col-md-2">Language : </Col>
        <Col className="col-8 col-md-4 t-color">{userProfile.language}</Col>
        <Col className="col-4 col-md-2">Strength : </Col>
        <Col className="col-8 col-md-4 t-color">{userProfile.strength}</Col>
        <Col className="col-4 col-md-2">Skype Id : </Col>
        <Col className="col-8 col-md-4 t-color">{userProfile.skypeId}</Col>
        <Col className="col-4 col-md-2">Permanent Address : </Col>
        <Col className="col-8 col-md-4 t-color">
          {userProfile.permanentAddress
            ? parse(`${userProfile.permanentAddress}`)
            : "N/A"}
        </Col>
        <Col className="col-12 col-md-2">Career Objectives : </Col>
        <Col className="col-12 col-md-10 t-color">
          {userProfile.careerObjective
            ? parse(`${userProfile.careerObjective}`)
            : "N/A"}
        </Col>
        <Col className="col-12 col-md-2">Career Summary : </Col>
        <Col className="col-12 col-md-10 t-color">
          {userProfile.careerSummary
            ? parse(`${userProfile.careerSummary}`)
            : "N/A"}
        </Col>
        <Col className="col-12 col-md-2">Personal Qualities : </Col>
        <Col className="col-12 col-md-10 t-color">
          {userProfile.personalQualities
            ? parse(`${userProfile.personalQualities}`)
            : "N/A"}
        </Col>
      </Row>
    </>
  );
};

export default UserProfile;
