import React from "react";
import { useInView } from "react-intersection-observer";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTrash } from "@fortawesome/free-solid-svg-icons";
import { Col, FormGroup, Input, Row } from "reactstrap";

const ProfileAcademySection = (props) => {
  const item = props.item;
  const { ref, inView } = useInView({
    triggerOnce: true, // Only trigger the animation once
    threshold: 0.1, // Percentage of section visibility to trigger the animation
  });

  return (
    <div ref={ref} className={inView ? "animated-section" : "hidden-section"}>
      <Row key={item._id} className="t-color px-3 mx-0">
        <Col className="order-1 flex-sm-grow-1 flex-md-grow-0 col-md-3 p-color ps-0 ps-md-2">
          {item.academyName}
        </Col>
        <Col className="order-3 order-md-2 flex-md-grow-1 p-0 pe-2 col-12 col-md-7">
          {item.instituteName}, {item.passingYear}
        </Col>
        <Col className="order-2 order-md-3 flex-sm-grow-1 flex-md-grow-0 col-2 col-md-1 p-0 clear-fix">
          <FormGroup switch className="float-end">
            <Input
              role="switch"
              type="switch"
              checked={item.active}
              onChange={(e) => props.updateStatus(item._id, !item.active)}
            />
            <span className="d-none d-md-inline">
              {item.active ? "Active" : "Inactive"}
            </span>
          </FormGroup>
        </Col>
        <Col className="order-2 order-md-3 flex-sm-grow-1 flex-md-grow-0 p-0 col-2 col-md-1">
          <div>
            <FontAwesomeIcon
              icon={faEdit}
              style={{ padding: "7px 7px" }}
              onClick={(e) => props.editHandler(item)}
            />
            <FontAwesomeIcon
              icon={faTrash}
              style={{ padding: "7px 7px" }}
              onClick={(e) => props.deleteHandler(item)}
            />
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default ProfileAcademySection;
