import React, { useState } from "react";
import { useInView } from "react-intersection-observer";
import { Col, FormGroup, Input, Row } from "reactstrap";
import parse from "html-react-parser";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../../reducers/homeReducer";
import FileComponent from "../../shared-components/FileComponent";

const HomeContent2 = (props) => {
  const homeData = props.homeData;
  const { userdetails } = useSelector((state) => state.user);
  const [pageContent2, setPageContent2] = useState(homeData.pageContent2);
  const dispatch = useDispatch();

  const { ref, inView } = useInView({
    triggerOnce: true, // Only trigger the animation once
    threshold: 0.1, // Percentage of section visibility to trigger the animation
  });

  const updateStatus = async () => {
    await dispatch(
      updateHomeReducer({
        ...homeData,
        pageContent2: { ...pageContent2, active: !pageContent2.active },
        token: userdetails.token,
      })
    );
    props.updateData({
      ...homeData,
      pageContent2: { ...pageContent2, active: !pageContent2.active },
    });
    setPageContent2({ ...pageContent2, active: !pageContent2.active });
  };
  return (
    <div ref={ref} className={inView ? "animated-section" : "hidden-section"}>
      <Row>
        <Col sx={6}>Status</Col>
        <Col sx={6}>
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={pageContent2.active}
              onChange={updateStatus}
            />
            {pageContent2.active ? "Active" : "Inactive"}
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col sx={6}>Content Title</Col>
        <Col sx={6}>{parse(`${pageContent2.contentTitle}`)}</Col>
      </Row>
      <Row>
        <Col sx={6}>Content Heading</Col>
        <Col sx={6}>{parse(`${pageContent2.contentHeading}`)}</Col>
      </Row>
      <Row>
        <Col sx={6}>Description</Col>
        <Col sx={6}>{parse(`${pageContent2.contentData}`)}</Col>
      </Row>
      <Row>
        <Col sx={6}>Attachment</Col>
        <Col sx={6}>
          {pageContent2.attachment ? (
            <FileComponent
              fileSrc={pageContent2.attachment}
              alt={pageContent2.contentHeading}
              fileStyle={{ maxHeight: "80px" }}
            />
          ) : (
            ""
          )}
        </Col>
      </Row>
    </div>
  );
};

export default HomeContent2;
