import React, { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { Col, FormGroup, Input, Row } from "reactstrap";
import parse from "html-react-parser";
import { useDispatch, useSelector } from "react-redux";
import { updateHomeReducer } from "../../reducers/homeReducer";
import FileComponent from "../../shared-components/FileComponent";

const HomeTopBanner = (props) => {
  const homeData = props.homeData;
  const { userdetails } = useSelector((state) => state.user);
  const [banner, setBanner] = useState(props.homeData.banner);
  const dispatch = useDispatch();

  const { ref, inView } = useInView({
    triggerOnce: true, // Only trigger the animation once
    threshold: 0.1, // Percentage of section visibility to trigger the animation
  });

  const updateStatus = async () => {
    await dispatch(
      updateHomeReducer({
        ...homeData,
        banner: { ...banner, active: !banner.active },
        token: userdetails.token,
      })
    );

    props.updateData({
      ...homeData,
      banner: { ...banner, active: !banner.active },
    });
    setBanner({ ...banner, active: !banner.active });
  };

  useEffect(() => {
    setBanner(props.homeData.banner);
  }, [props.homeData.banner]);

  return (
    <div ref={ref} className={inView ? "animated-section" : "hidden-section"}>
      <Row>
        <Col md={6}>Status : </Col>
        <Col md={6}>
          <FormGroup switch>
            <Input
              role="switch"
              type="switch"
              checked={banner.active}
              onChange={updateStatus}
            />
            {banner.active ? "Active" : "Inactive"}
          </FormGroup>
        </Col>
      </Row>
      <Row>
        <Col md={6}>Banner Image : </Col>
        <Col md={6}>
          {banner.bannerImage ? (
            <FileComponent
              fileSrc={banner.bannerImage}
              alt="Code Folio"
              fileStyle={{ maxHeight: "80px" }}
            ></FileComponent>
          ) : (
            <img
              src={`/images/no-image-banner.jpg`}
              alt="UI/UX Design"
              style={{ maxHeight: "80px" }}
            />
          )}
        </Col>
      </Row>
      <Row>
        <Col md={6}>Banner Intro Line 1 : </Col>
        <Col md={6}>{parse(`${banner.introductionL1}`)}</Col>
      </Row>
      <Row>
        <Col md={6}>Banner Intro Line 2 : </Col>
        <Col md={6}>{parse(`${banner.introductionL2}`)}</Col>
      </Row>
    </div>
  );
};

export default HomeTopBanner;
